import * as THREE from 'three'
import { NormalBlending } from 'three';

var mesh, clock, cubeSineDriver,
    smokeMaterial, smokeGeo, smokeParticles, delta;


let galaxyColors = [
    new THREE.Color("#a225db").multiplyScalar(2.5),
    new THREE.Color("#db2540").multiplyScalar(2.5),
    new THREE.Color("#db2540").multiplyScalar(2.5),
    new THREE.Color("#254cdb").multiplyScalar(2.5)
];

export function init(scene) {
    clock = new THREE.Clock();



    smokeGeo = new THREE.PlaneGeometry(0.7, 0.7);
    smokeParticles = [];


    for (var p = 0; p < 100; p++) {
        smokeMaterial = new THREE.MeshLambertMaterial
            ({
                color: galaxyColors[Math.floor(Math.random() * galaxyColors.length)],
                map: new THREE.TextureLoader().
                    load("https://iili.io/bzbRnt.png"),
                opacity: 0.05,
                blending: NormalBlending,
                depthWrite: false,
                depthTest: true,
                transparent: true,
                fog: true
            });

        var particle = new THREE.Mesh(smokeGeo, smokeMaterial);
        var radius = 2;
        particle.position.set(Math.random() * radius - radius / 2, Math.random() * .01 - .3, Math.random() * radius - radius / 2);
        particle.rotation.z = Math.random() * 360;
        //var randomscale = Math.random() * 2 + .5;
        //particle.scale.set(randomscale, randomscale, randomscale);
        scene.add(particle);
        smokeParticles.push(particle);
    }
    animate();
}

function animate() {
    delta = clock.getDelta();
    requestAnimationFrame(animate);
    evolveSmoke();
}

function evolveSmoke() {
    var sp = smokeParticles.length;
    while (sp--) {
        smokeParticles[sp].rotation.z += (delta * 0.03);
    }
}



